* {
    margin: 0;
    padding: 0;
  }
  
  /* background---------------------- */
  
  body {
    background-size: cover !important;
    font-family: poppins;
    background-color: black;
  }
  .fix {
    z-index: -8;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%); /* IE 9 */
    -webkit-transform: translate(-50%, -50%); /* Chrome, Safari, Opera */
  }
  .sponsor {
    z-index: -8;
    position: absolute;
    top: 210%;
    left: 51%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%); /* IE 9 */
    -webkit-transform: translate(-50%, -50%); /* Chrome, Safari, Opera */
  }
  @media only screen and (max-width: 370px) and (max-height: 700px) {
    .sponsor {
      top: 240%;
      left: 57%;
    }
  }
  @media only screen and (min-width: 374px) and (max-height: 700px) and (max-width: 400px) {
    .sponsor {
      top: 230%;
      left: 54%;
    }
  }
  .jarvis-footer {
    position: absolute;
    top: 230%;
    width: 100%;
  }
  @media only screen and (max-width: 380px) and (max-height: 700px) {
    .jarvis-footer {
      top: 260%;
    }
  }
  /* --------------NAV BAR-------------- */
  
  .navCustom {
    background-color: transparent;
    padding: 0 0 !important;
    margin: 0 0 !important;
    position: fixed !important;
  }

  @media only screen and (min-width: 300px){
    .navbar-toogler{
      
    }
  }
  
  /* ----OWASP LOGO------ */
  
  .navbar-brand .logo {
    height: 4vw;
    width: auto;
    margin-left: 6rem;
  }


  
  .ml-auto {
    margin-right: auto !important;
  }
  
  .navbar-collapse ul {
    align-items: center !important;
  }
  .backBtn {
    font-family: poppins;
    size: 3rem;
    padding: 0.2rem 1rem;
    margin-right: 8rem;
    color: #fff;
    position: relative;
    border: 2px solid #fff;
    transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
  }
  
  .backBtn:hover {
    color: #000 !important;
    background-color: white;
    text-shadow: none;
  }
