  }
  .nav-link {
    color: rgb(255, 217, 217) !important;
    font-family: poppins;
    font-size: 1rem;
  }
  .nav-link:hover {
    color: rgb(255, 255, 255) !important;
    text-decoration: underline !important;
  }
  .li-234 {
