  .dashbaord {
    margin-left: 0 !important;
    text-decoration: underline !important;
    font-weight: bold !important;
  }
  
  /* ------SIGN IN BUTTON------- */
  
  a.signin {
    font-family: poppins;
    size: 3rem;
    padding: 0.2rem 1rem;
    margin-right: 8rem;
    color: #fff;
    position: relative;
    border: 2px solid #fff;
    transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
  }
  
  a.signin:hover {
    color: #000 !important;
    background-color: transparent;
    text-shadow: none;
  }
  
  a.signin:hover:before {
    bottom: 0%;
    top: auto;
    height: 100%;
  }
  
  a.signin:before {
    display: block;
    position: absolute;
    left: 0px;
    top: 0px;
    height: 0px;
    width: 100%;
    z-index: -1;
    content: "";
    color: #000 !important;
    background: #fff;
    transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
  }
  
