body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html,
body {
  max-width: 100%;
  overflow-x: hidden;
  font: Pricedown;
}
body {
  /* width: 100vh; */
  height: 100vh;
  background-color: white;
    -webkit-animation: bgfade 15s infinite;
            animation: bgfade 15s infinite;
      background-size: cover;
      background-position: center;
      background-color: #000;
      position: -webkit-sticky;
      position: sticky;
  /* background-image: radial-gradient(
      circle at top right,
      rgba(121, 68, 154, 0.13),
      transparent
    ), */
    
    /* radial-gradient(circle at 20% 80%, rgba(41, 196, 255, 0.13), transparent); */

    /* background: linear-gradient(to right,#a89f902d,transparent ); */
  background-repeat: no-repeat;
} 
/****** add a background overlay *******/
body:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to bottom, #000000 75%, #00000000);
  opacity: 0.35;
}
.login-wrap h1 {
  margin-left: 13% !important;
}
.color {
  color: #fff !important;
}
canvas {
  position: fixed;
  width: 100%;
  height: 100%;
}

/* .logo{
    margin-top: 100px;
    margin-left:500px;
  } */

.logo {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.landingpart {
  height: 100%;
}
.bounds {
  overflow-x: hidden;
  width: 100% !important;
}

.logojarvis {
  margin: auto;
  width: 75%;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.prop {
  margin-top: 15rem;
}
@-webkit-keyframes bgfade {
  0% {
    background-image: url(/static/media/bg1.18d9ce70.jpeg);
  }

  25% {
    background-image: url(/static/media/bg2.2a429e9b.jpeg);
  }

  50% {
    background-image: url(/static/media/bg3.2bbadb6d.jpeg);
  }

  75% {
    background-image: url(/static/media/bg4.d70a8f2e.jpeg);
  }

  100% {
    background-image: url(/static/media/bg1.18d9ce70.jpeg);
  }
}
@keyframes bgfade {
  0% {
    background-image: url(/static/media/bg1.18d9ce70.jpeg);
  }

  25% {
    background-image: url(/static/media/bg2.2a429e9b.jpeg);
  }

  50% {
    background-image: url(/static/media/bg3.2bbadb6d.jpeg);
  }

  75% {
    background-image: url(/static/media/bg4.d70a8f2e.jpeg);
  }

  100% {
    background-image: url(/static/media/bg1.18d9ce70.jpeg);
  }
}



